import React, {Component} from 'react';
import { Link } from "react-router-dom";
import "./style.css";

import axios from "axios";

class Homepage extends Component {
   constructor() {
      super()
      this.state = {
          allUsers: null,
          allUsernames: null,
          allEmails: null
      }
   }

   getAllCharacters = () => {
       const characters = [];
        for(let count = 0; count < 26; count++){
            characters.push(<div className={`character character${count+1}`} />)
        }
        return characters;
   }

   splitUserInfo = (usersArray) => {
       const allUsers = usersArray;
       const allUsernames = [];
       const allEmails = [];
       usersArray.map(user => {
           allUsernames.push(user.username);
           allEmails.push(user.email);
           return "";
       })
       this.setState({allUsers, allUsernames, allEmails})
   }

   fetchAllUsersData = async () => {
       console.log("here");
       await axios.get("http://localhost:4567/users").then(response => {
        console.log(response);
         this.splitUserInfo(response.data.allUsers);
       });  
   }

   componentDidMount(){
       this.fetchAllUsersData();
   }

   render() {
      const characters = this.getAllCharacters()
      return (
        <div className="home-page">
          <h1 className="game-title">Combat Colosseum</h1>
          <div className="all-characters">{characters}</div>
          <Link
            to={{ pathname: "/login", state: { usersInfo: this.state } }}
          >
            <button className="home-page-enter-button">START!</button>
          </Link>
        </div>
      );
   }
}

export default Homepage;