import React, {Component} from 'react';
import { Link, Redirect } from "react-router-dom";
import "./style.css";

class LoginPage extends Component {
   constructor() {
      super()
      this.state = {
        invalidUserInfo: false,
        loggedOn: false
      }
   }

   handleChange = (evnt) => {
       this.setState({ [evnt.target.name]: evnt.target.value, invalidUserInfo: false});
   }

   handleSubmit = (evnt) => {
       evnt.preventDefault();
       const allUsers = this.props.location.state.usersInfo.allUsers;
       for( let index = 0;  index < allUsers.length; index++){
           if (allUsers[index].username === this.state.username && allUsers[index].password === this.state.password){    
               this.props.currentUserInfo(allUsers[index].id, allUsers[index].username)
               this.setState({loggedOn: true})
           }
           else if (index === allUsers.length-1){
                this.setState({invalidUserInfo: true})
           }
       }

   }

   render() {
      return (
        !this.state.loggedOn ?
        <div className="login-page">
        <h1 className="game-title">Combat Colosseum</h1>
        <form className="login-form" onChange={this.handleChange} onSubmit={this.handleSubmit}>
            {this.state.invalidUserInfo ? <p className="login-error-message">Invalid Username or Password</p> : null}
            <label className="login-input-label">
                Username
                <br/>
                <input className="login-input" type="text" name="username" placeholder="Username" />
            </label>
            <br/>
            <label className="login-input-label">
                Password
                <br/>
                <input className="login-input" type="password" name="password" placeholder="Password" />
            </label>
            <br />
            <button className="login-button">Login</button>
            <p className="sign-up-message">Don't have an account? <Link to={{pathname:"/signup", state:{emails:this.props.location.state.usersInfo.allEmails, usernames:this.props.location.state.usersInfo.allUsernames}}}>Sign Up</Link></p>
        </form>
        </div>
        : 
        <Redirect to="/mainMenu" />
      )
   }
}

export default LoginPage;