import React, {Component} from 'react';
import { Redirect } from "react-router-dom";
import "./style.css";

class SinglePlayer extends Component {
   constructor(props) {
      super(props)
      this.state = {
        goToLeaderboard:  false,
        gameOver: false,
        playerStyles:{
            marginLeft: 25,
            marginTop: 370,
            width: "200px",
            height: "200px"
        },
        enemyStyles:{
            marginLeft: 800,
            marginTop: 370,
            width: "200px",
            height: "200px"
        },
        playerHpStyles:{
            background: "lime",
            width: 500,
            height: 20
        },
        enemyHpStyles:{
            background: "lime",
            width: 500,
            height: 20
        },
        playerBlocking: false,
        playerBlockingStyles:{
            position: "absolute",
            width: "230px",
            height:"230px",                            
        },
        enemyBlocking: false,
        enemyBlockingStyles:{
            position: "absolute",
            width: "230px",
            height:"230px",
        },
        playerImageSource:require(`../../Assets/GIFs/${this.props.location.state.selectedCharacter}/${this.props.location.state.selectedCharacter}-standing.gif`),
        canAttack: true,
        currentEnemy: null,
        enemyImageSource: null,
        characters: [...this.props.location.state.characterImagesLeft, ...this.props.location.state.characterImagesRight],
        usedCharacters: [this.props.location.state.selectedCharacter],
        enemyLogicLoop: null,
        playerScore: 0,
      }
   }

   resetPlayerImage = (info) => {
       let playerStyles = {...this.state.playerStyles, width:200};
       const playerImageSource = require(`../../Assets/GIFs/${info.selectedCharacter}/${info.selectedCharacter}-standing.gif`)
       this.setState({ playerStyles, playerImageSource })
   }

   lowerPlayerHp = () => {
       let width = this.state.playerHpStyles.width - 50;
       let background = (width <= 400 && width >= 300) ? "yellow" : 
       ((width <= 250 && width >= 150) ? "orange" : 
       (width <= 100 && width >= 50) ? "red" : "lime")
       let playerHpStyles = {...this.state.playerHpStyles, width, background}
       const playerImageSource = width === 0 ? 
       require(`../../Assets/GIFs/${this.props.location.state.selectedCharacter}/${this.props.location.state.selectedCharacter}-dying.gif`)
        : this.state.playerImageSource;
        width === 0 ?
       this.setState({playerHpStyles, playerImageSource, goToLeaderboard: true}):
       this.setState({playerHpStyles, playerImageSource})
   }

   lowerEnemyHp = () => {
       let width = this.state.enemyHpStyles.width - 50;
       let background = (width <= 400 && width >= 300) ? "yellow" : 
       ((width <= 250 && width >= 150) ? "orange" : 
       (width <= 100 && width >= 50) ? "red" : "lime")
       let enemyHpStyles = {...this.state.enemyHpStyles, width, background}
       const enemyImageSource = width === 0 ? 
       require(`../../Assets/GIFs/${this.state.currentEnemy}/${this.state.currentEnemy}-dying-left.gif`)
        : this.state.enemyImageSource;
       this.setState({enemyHpStyles, enemyImageSource});
       if (width === 0){
           clearInterval(this.state.enemyLogicLoop);
           let playerScore = this.state.playerScore;
           playerScore += (this.state.playerHpStyles.width/10)
           this.setState({enemyLogicLoop: null, playerScore})
           setTimeout(() => {this.selectNextEnemy()}, 2000)
        };

   }

   handleKeyDown = (evnt) => {
       const info = this.props.location.state;
       let marginLeft = this.state.playerStyles.marginLeft;
       let playerImageSource = this.state.playerImageSource;
       let width = "200px";
       let height = "200px";
        switch(evnt.keyCode){
            case 37:
                if(!this.state.playerBlocking){
                    marginLeft = marginLeft - 20 > 0 ? marginLeft - 20 : 0;
                    playerImageSource = require(`../../Assets/GIFs/${info.selectedCharacter}/${info.selectedCharacter}-running.gif`)
                }
            break;
            case 39:
                if(!this.state.playerBlocking){
                    marginLeft = this.state.enemyStyles.marginLeft - this.state.playerStyles.marginLeft === 135 ? marginLeft : marginLeft+20;
                    playerImageSource = require(`../../Assets/GIFs/${info.selectedCharacter}/${info.selectedCharacter}-running.gif`)
                }
            break;
            case 32:
                if(this.state.canAttack && !this.state.playerBlocking){
                    playerImageSource = require(`../../Assets/GIFs/${info.selectedCharacter}/${info.selectedCharacter}-attack.gif`)
                    width = ["fairy", "fairy-2", "fairy-3", "elf-wizard", "elf-archer", "warrior-2"].includes(info.selectedCharacter) ? 400 : 200; 
                    setTimeout(() => {this.resetPlayerImage(info)}, 650)
                    if(((this.state.enemyStyles.marginLeft - this.state.playerStyles.marginLeft) <= 250) && !this.state.enemyBlocking){
                        this.lowerEnemyHp()
                    }
                    this.setState({canAttack: false});
                }
            break;
            case 66:
                const playerBlockingStyles = {...this.state.playerBlockingStyles, marginLeft:this.state.playerStyles.marginLeft, marginTop:this.state.playerStyles.marginTop}
                this.setState({playerBlockingStyles, playerBlocking:true});
            break;
            default:
            break;
        }
        const playerStyles = {marginLeft, marginTop: 370, width, height };  
        this.setState({playerStyles, playerImageSource});    
   }

   handleKeyUp = (evnt) => {
       const info = this.props.location.state;
        if([37, 39].includes(evnt.keyCode)){
            setTimeout(() => {this.resetPlayerImage(info)}, 1);
        }
        else if(evnt.keyCode === 32){
            setTimeout(() => {this.setState({ canAttack: true })}, 2000)
        }
        else if(evnt.keyCode === 66){
            this.setState({playerBlocking: false})
        }
   }

   enemyLogic = () => {
        let choices;
        switch(true){
            case (this.state.enemyStyles.marginLeft - this.state.playerStyles.marginLeft) <= 250:
                choices = ["attack", "nothing", "attack"];
            break;
            case (this.state.enemyStyles.marginLeft > 700 && this.state.enemyStyles.marginLeft < 935 ):
                choices = ["forward", "nothing", "forward"];
            break;
            default:
                choices = ["backward", "nothing", "forward", "forward"];
            break;
        }
        let choice = choices[Math.floor(Math.random()*choices.length)];
        let enemyImageSource = this.state.enemyImageSource;
        let marginLeft = this.state.enemyStyles.marginLeft;
        let width;
        let enemyBlockingMarginLeft;
        switch(choice){
            case "attack":
                enemyImageSource = require(`../../Assets/GIFs/${this.state.currentEnemy}/${this.state.currentEnemy}-attack-left.gif`)
                width = ["fairy", "fairy-2", "fairy-3", "elf-wizard", "elf-archer", "warrior-2"].includes(this.state.currentEnemy) ? 300 : 200;
                if(!this.state.playerBlocking){
                    this.lowerPlayerHp();
                }
            break;
            case "forward":
                enemyImageSource = require(`../../Assets/GIFs/${this.state.currentEnemy}/${this.state.currentEnemy}-running-left.gif`)
                marginLeft -= 20;
            break;
            case "backward":
                enemyImageSource = require(`../../Assets/GIFs/${this.state.currentEnemy}/${this.state.currentEnemy}-running-left.gif`)
                marginLeft += 20;
            break;
            case "block":
                const enemyBlockingStyles = {...this.state.enemyBlockingStyles, marginLeft:this.state.enemyStyles.marginLeft, marginTop:this.state.enemyStyles.marginTop}
                setTimeout(() => {this.setState({enemyBlocking:false})}, 850);
                this.setState({enemyBlockingStyles, enemyBlocking:true});
            break;
            default:
            enemyImageSource = require(`../../Assets/GIFs/${this.state.currentEnemy}/${this.state.currentEnemy}-standing-left.gif`)
            break;
        }
        const enemyStyles = {...this.state.enemyStyles, marginLeft, width};
        enemyBlockingMarginLeft = width === 300 ? marginLeft + 200 : marginLeft;
        const enemyBlockingStyles = {...this.state.enemyBlockingStyles, enemyBlockingMarginLeft, width}
        this.setState({enemyImageSource, enemyStyles, enemyBlockingStyles});
   }

   selectNextEnemy = () => {
        const enemyHpStyles = {
            background: "lime",
            width: 500,
            height: 20
        }
       const characters = this.state.characters;
       const usedCharacters = this.state.usedCharacters;
       let randIndex = Math.floor(Math.random()*characters.length)
       while(usedCharacters.includes(characters[randIndex]) && usedCharacters.length !== characters.length-6 && !["fairy", "fairy-2", "fairy-3", "elf-wizard", "elf-archer", "warrior-2"].includes(characters[randIndex])){
           randIndex = Math.floor(Math.random() * characters.length);
       }
       if(usedCharacters.length === characters.length){
            this.setState({gameOver: true});
       }
       else{
            const currentEnemy = characters[randIndex];
            usedCharacters.push(currentEnemy);
            const enemyImageSource = require(`../../Assets/GIFs/${currentEnemy}/${currentEnemy}-standing-left.gif`);
            const enemyLogicLoop = setInterval(() => {this.enemyLogic()}, 1000);
            this.setState({usedCharacters, currentEnemy, enemyImageSource, enemyHpStyles, enemyLogicLoop});
       }
   }

   componentDidMount(){
        this.selectNextEnemy();
   }

   componentWillUnmount(){
        clearInterval(this.state.enemyLogicLoop);
   }

   render() {
       if(this.state.goToLeaderboard){ setTimeout(() => {this.setState({gameOver: true})}, 2500)}
      return (
          this.state.gameOver 
          ?
            <Redirect to={{pathname:"/leaderboards", state:{score:this.state.playerScore}}} /> 
          :
         <div className="single-player-mode" tabIndex="0" onKeyDown={this.handleKeyDown} onKeyUp={this.handleKeyUp}>
            <div className="player-hp-bar hp-bar" style={this.state.playerHpStyles}></div>
            <p className="name player-name">{this.props.currentUsername}</p>
            <p className="player-score">SCORE: {this.state.playerScore}</p>
            <div className="enemy-hp-bar hp-bar" style={this.state.enemyHpStyles}></div>
            <p className="name enemy-name">CPU</p>
            <img className="versus" src="http://static1.comicvine.com/uploads/original/11112/111129141/5440487-1122329314-52705.png" alt="VS" />
            <img className="single-mode-character single-mode-character-player" style={this.state.playerStyles} src={this.state.playerImageSource} alt=""/>
            {this.state.playerBlocking ? <img style={this.state.playerBlockingStyles} src="https://opengameart.org/sites/default/files/spr_shield.png" alt="" /> : null}
            <img className="single-mode-character single-mode-character-enemy" style={this.state.enemyStyles} src={this.state.enemyImageSource} alt=""/>
            {this.state.enemyBlocking ? <img style={this.state.enemyBlockingStyles} src="https://opengameart.org/sites/default/files/spr_shield.png" alt="" /> : null}
            {this.state.goToLeaderboard ? <h1 className="game-over">Game Over!</h1> : null}
         </div>
      )
   }
}

export default SinglePlayer;