import React, {Component} from 'react';
import "./style.css";
import axios from "axios";

class SignUpPage extends Component {
   constructor() {
      super();
      this.state = {
        invalidEmail: false,
        invalidUsername: false,
        invalidPassword: false
      }
   }

    handleChange = (evnt) => {
        this.setState({
          [evnt.target.name]: evnt.target.value,
          invalidEmail: false,
          invalidUsername: false,
          invalidPassword: false
        });
    }

    isValidInput = () => {
        let isValid = false
        switch (true) {
            case this.props.emails.includes(this.state.email):
                this.setState({ invalidEmail: true });
            break;
            case this.props.usernames.includes(this.state.username):
                this.setState({ invalidUsername: true });
            break;
            case this.state.password !== this.state.confirmPassword:
                this.setState({ invalidPassword: true });
            break;
            default:
            return true;
        }
        return isValid;
    }

    handleSubmit = async (evnt) => {
        evnt.preventDefault();
        if(this.isValidInput()){
            const newUser = {
                email: this.state.email,
                username: this.state.username,
                password: this.state.password
            }
            await axios.post("http://localhost:4567/users", newUser);
            const newestUser = await axios.get(`http://localhost:4567/users/${newUser.username}`);
            const user = newestUser.data.thisUser[0];
            this.props.currentUserInfo(user.id, user.username);
        }
    }

   render() {
      return (
         <div className="sign-up-page">
            <h1 className="sign-up-game-title">Combat Colosseum</h1>
            <form className="sign-up-form" onChange={this.handleChange} onSubmit={this.handleSubmit}>
                <label className="sign-up-input-label">
                    Email
                    <br/>
                    <input className="sign-up-input" type="text" name="email" placeholder="Email" />
                </label>
                {this.state.invalidEmail ? <p className="sign-up-error-message">Email already in use!</p> : null}
                <br/>
                <label className="sign-up-input-label">
                    Username
                    <br/>
                    <input className="sign-up-input" type="text" name="username" placeholder="Username" />
                </label>
                {this.state.invalidUsername ? <p className="sign-up-error-message">Username is taken!</p> : null}
                <br/>
                <label className="sign-up-input-label">
                    Password
                    <br/>
                    <input className="sign-up-input" type="password" name="password" placeholder="Password" />
                </label>
                {this.state.invalidPassword ? <p className="sign-up-error-message">Passwords do not match!</p> : null}
                <br />
                <label className="sign-up-input-label">
                    Confirm Password
                    <br/>
                    <input className="sign-up-input" type="password" name="confirmPassword" placeholder="Confirm Password" />
                </label>
                {this.state.invalidPassword ? <p className="sign-up-error-message">Passwords do not match!</p> : null}
                <br />
                <button className="sign-up-button">Create Account</button>
            </form>
         </div>
      )
   }
}

export default SignUpPage;