import React, {Component} from 'react';
import "./style.css";

import CurrentSelectedCharacter from "../CurrentSelectedCharacter";


class CharacterSelect extends Component {
   constructor() {
      super()
      this.state = {
        characterImagesRight: ["fairy-2", "knight-2", "samurai", "viking-3", "warrior-3", "samurai-2", "fairy-3", "elf-princess", "viking"],
        characterImagesLeft: ["knight-3", "elf-archer", "fairy", "warrior", "knight-4", "viking-2", "elf-wizard", "ninja-2", "warrior-2"],
        characterActions: ["standing", "attack", "standing-left", "attack-left"],
        selectedCharacter: null,
        selectedCharacterIndex: null
      };
   }

   chooseCharacter = (selectedCharacter, selectedCharacterIndex) => {
       this.setState({ selectedCharacter, selectedCharacterIndex });
   }

   render() {
       const leftSideCharacters = this.state.characterImagesLeft.map((character, index) => (
         <div key={index} className="character-square" onClick={()=>{this.chooseCharacter(character, index)}}>
           <img src={require(`../../Assets/Images/${character}.png`)} alt={character} />
         </div>
       ));
       const rightSideCharacters = this.state.characterImagesRight.map((character, index) => (
         <div key={index} className="character-square" onClick={()=>{this.chooseCharacter(character, index)}}>
           <img src={require(`../../Assets/Images/${character}.png`)} alt={character} />
         </div>
       ));
      return (
        <div className="character-select-page">
          <h1>Choose Your Character</h1>
          <div className="character-display">
            <div className="characters characters-right">{rightSideCharacters}</div>
            <CurrentSelectedCharacter {...this.state} />
            <div className="characters characters-left">{leftSideCharacters}</div>
          </div>
        </div>
      );
   }
}

export default CharacterSelect;