import React, { Component } from "react";
import { Redirect, Switch, Route } from "react-router-dom";
import "./style.css";
import Homepage from "../Components/Homepage";
import MainMenu from "../Components/MainMenu";
import CharacterSelect from "../Components/CharacterSelect";
import SinglePlayer from "../Components/SinglePlayer";
import Leaderboards from "../Components/Leaderboards";
import LoginPage from "../Components/LoginPage";
import SignUpPage from "../Components/SignUpPage";

class App extends Component {
  constructor(){
    super()
    this.state = {
      loggedIn:false
    }
  }

  logOutCurrentUser = () => {
    localStorage.setItem("userLoggedOn", "false");
    localStorage.setItem("currentUsername", null);
    localStorage.setItem("currentUserId", null);
    this.setState({ loggedIn:false });
  }

  currentUserInfo = (currentUserId, currentUsername) => {
    localStorage.setItem("userLoggedOn", "true");
    localStorage.setItem("currentUsername", currentUsername);
    localStorage.setItem("currentUserId", currentUserId);
    this.setState({loggedIn: true})
  }

  componentDidMount(){
    localStorage.setItem("userLoggedOn", "false")
  }

  render() {
    const userLoggedOn = localStorage.getItem("userLoggedOn")
    const username = localStorage.getItem("currentUsername")
    const userId = localStorage.getItem("currentUserId")
    return (
    <div className="App">
      <Switch>
        <Route exact path="/" render={() => <Homepage /> } />
        <Route path="/login" render={(props) =>  <LoginPage {...props} currentUserInfo={this.currentUserInfo} /> } />
        <Route path="/signup" render={(props) => <SignUpPage {...props} currentUserInfo={this.currentUserInfo} />} />
        {!userLoggedOn === "true" ? null :
        <div>
          <Route path="/mainMenu" render={() => <MainMenu currentUsername={username} />} />
          <Route path="/characterSelect" render={() => <CharacterSelect currentUsername={username} />} />
          <Route path="/singlePlayer" render={(props) => <SinglePlayer {...props} currentUsername={username} />}/>
          <Route path="/leaderboards" render={(props) => <Leaderboards {...props} currentUsername={username} currentUserId={userId} logOut={this.logOutCurrentUser} />}/>
        </div>
        }
        <Route render={() => <Redirect to="/" />} />
      </Switch>
    </div>
    );
  }
}

export default App;
