import React, {Component} from 'react';
import { Link, Redirect } from "react-router-dom";
import axios from 'axios';
import "./style.css";

class Leaderboards extends Component {
   constructor() {
      super()
      this.state = {
        scores: null,
        userLoggedIn: true
      }
   }

   logUserOut = () => {
       this.props.logOut();
       this.setState({userLoggedIn: false});
   }

   renderScores = (info) => {
       const userScores = info.scores;
       const usersNames = info.users;
       const allScores = userScores.map((score, index) =>
         usersNames[index] === this.props.currentUsername ? (
           <div className="current-user user-score" key={index}>
             <p className="rank">{index + 1}</p>
             <p className="username">{usersNames[index]}</p>
             <p className="score">{score.score}</p>
           </div>
         ) : (
           <div className="user-score" key={index}>
             <p className="rank">{index + 1}</p>
             <p className="username">{usersNames[index]}</p>
             <p className="score">{score.score}</p>
           </div>
         )
       );
       return allScores;
   }

   getAllScores = async () => {
       const response = await axios.get("http://localhost:4567/scores")
        Promise.all(response.data.allScores.map(async score => {
           const response2 = await axios.get(`http://localhost:4567/users/id/${score.userId}`)
           return response2.data.thisUser.username;
       })
       ).then((promises) => {
            const users = promises;
            const scores = response.data.allScores;
            this.setState({users, scores });
       })
   }

   addUsersScore = async () => {
       const userInfo = this.props.location.state
       const userId = Number(this.props.currentUserId );
       const newScore = {
           score: userInfo.score,
           userId: userId
       }
       await axios.post(`http://localhost:4567/scores`, newScore);
       this.getAllScores();
   }

   componentDidMount(){
       this.addUsersScore();
   }

   render() {
      const allScores = this.state.scores && this.renderScores(this.state);
      return (
        !this.state.userLoggedIn ? <Redirect to="/" /> :
        <div className="leaderboard-page">
          <h1 className="leaderboard-title">Leaderboards</h1>
          <div className="leaderboard">
            <div className="leaderboard-columns">
              <h3 className="column-rank">Rank</h3>
              <h3 className="column-username">Username</h3>
              <h3 className="column-score">Score</h3>
            </div>
            <div className="all-scores">{allScores}</div>
          </div>
          <Link to="/mainMenu" className="link link-to-main-menu">Main Menu</Link>
          <Link to="/characterSelect" className="link link-to-character-select">Play Again</Link>
          <p onClick={this.logUserOut} className="link link-to-home">Log Out</p>
        </div>
      );
   }
}

export default Leaderboards;