import fairy2Standing from "../../Assets/GIFs/fairy-2/fairy-2-standing.gif";
import fairy2Attack from "../../Assets/GIFs/fairy-2/fairy-2-attack.gif";
import samurai2Standing from "../../Assets/GIFs/samurai-2/samurai-2-standing.gif";
