import knight4Standing from "../../Assets/GIFs/knight-4/knight-4-standing-left.gif";
import knight4Attack from "../../Assets/GIFs/knight-4/knight-4-attack-left.gif";

import "./style.css";

class CurrentSelectedCharacter extends Component {
   constructor() {
      super()
      this.state = {
        characterImagesRightStandingFiles: [fairy2Standing, knight2Standing, samuraiStanding, viking3Standing, warrior3Standing, samurai2Standing, fairy3Standing, elfPrincessStanding, vikingStanding],
        characterImagesRightAttackFiles: [fairy2Attack, knight2Attack, samuraiAttack, viking3Attack, warrior3Attack, samurai2Attack, fairy3Attack, elfPrincessAttack, vikingAttack],
        characterImagesLeftStandingFiles: [knight3Standing, elfArcherStanding, fairyStanding, warriorStanding, knight4Standing, viking2Standing, elfWizardStanding, ninja2Standing, warrior2Standing],
        characterImagesLeftAttackFiles: [knight3Attack, elfArcherAttack,fairyAttack, warriorAttack, knight4Attack, viking2Attack, elfWizardAttack, ninja2Attack, warrior2Attack],
        currentCharacterSource: null
      }
   }

   displayCharacter = (character, index) => (
        this.props.characterImagesRight.includes(character)
          ? this.state.characterImagesRightStandingFiles[index]
          : this.state.characterImagesLeftStandingFiles[index]
   )

   render() {
      let currentCharacterSource = (this.props.selectedCharacter && this.displayCharacter(this.props.selectedCharacter, this.props.selectedCharacterIndex)) || this.state.characterImagesRightStandingFiles[0];
      const selectedCharacter = this.props.selectedCharacter || "fairy-2";
      const selectedCharacterIndex = this.props.selectedCharacterIndex || 0;
      return (
         <div>
            <img src={currentCharacterSource} className="current-character" alt=""/>
            <Link to={{pathname:"/singlePlayer", state:{...this.props, selectedCharacter:selectedCharacter, selectedCharacterIndex:selectedCharacterIndex}}}>
                <button className="start-game-button">Start Game</button>
            </Link>
         </div>
      )
   }
}

export default CurrentSelectedCharacter;